/**
 * @description
 * Repeats a string a specified number of times.
 * - https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/repeat
 *
 * @example
 * ``` ts
 * strRepeat('0', 3);
 * // => '000'
 * strRepeat(' 12 3', 1);
 * // => ' 12 3'
 * strRepeat('123', 2);
 * // => '123123'
 * ```
 *
 * @param str - The string to repeat.
 * @param count - The number of times to repeat the string.
 * @returns The repeated string.
 * @throws {RangeError} If the count is negative, equal to Infinity, or would result in a string that exceeds the maximum size.
 *
 * @docsCategory preference/utils/string
 * @codeFilter str-repeat
 */
export const strRepeat = (str = '', count = 0) => {
  if (count < 0) {
    throw new RangeError('repeat count must be non-negative');
  }
  if (count === Infinity) {
    throw new RangeError('repeat count must be less than infinity');
  }
  // floors and rounds-down it.
  count = count | 0;
  if (str.length === 0 || count === 0) {
    return '';
  }
  // Ensuring count is a 31-bit integer allows us to heavily optimize the
  // main part. But anyway, most current (August 2014) browsers can't handle
  // strings 1 << 28 chars or longer, so:
  if (str.length * count >= 1 << 28) {
    throw new RangeError('repeat count must not overflow maximum string size');
  }
  const rpts: string[] = [];
  for (let i = 0; i < count; i++) {
    rpts.push(str);
  }
  return rpts.join('');
};
